import React from 'react'
import { Link, NavLink, useNavigate } from 'react-router-dom'

export default function Navbar ({userData, logOut}){
  


  let navigate = useNavigate()


 
  

  


 
  return (
    <>
   
   
    <nav className="navbar navbar-expand-lg bg-body-tertiary fixed-top navbar-dark shadow">
    <div className="container">
    <nav className="navbar navbar-expand-lg bg-body-tertiary fixed-top navbar-dark shadow">
    <div className="container">
      
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          

            
            <a className='nav-link ' href="">Hello </a>


             {userData?<> 
             <li className="nav-item">
            <Link to='/Login' className='nav-link' onClick={logOut}>logout</Link>
                    
                    
          </li> </>: <> <li className="nav-item">
            <NavLink  className={({ isActive  }) =>
                      isActive
                        ? " nav-link"             : 'nav-link'                                            
                    } to="">register</NavLink>
                    </li>


          <li className="nav-item">
            <NavLink  className={({ isActive  }) =>
                      isActive
                        ? " nav-link"             : 'nav-link'                                            
                    }  to='login'>login</NavLink>
          </li>
          </>}






          

         
            


        
          
          {userData?
            <>
      
          <li className="nav-item">
            <NavLink  className={({ isActive  }) =>
                      isActive
                        ? " nav-link"             : 'nav-link'                                            
                    }  to="home">Home</NavLink>
          </li>
          <li className="nav-item">
            <NavLink  className={({ isActive  }) =>
                      isActive
                        ? " nav-link"             : 'nav-link'                                            
                    }  to="people">people</NavLink>
          </li>
          
         
          
          <li className="nav-item">
            <NavLink  className={({ isActive  }) =>
                      isActive
                        ? " nav-link"             : 'nav-link'                                            
                    }  to="movies">movies</NavLink>
          </li>
          <li className="nav-item">
            <NavLink  className={({ isActive  }) =>
                      isActive
                        ? " nav-link"             : 'nav-link'                                            
                    }  to="tvshow">tvshow</NavLink>
          </li> </> :''}
          


            
          
          
          
        </ul>             
        
      </div>
    </div>
  </nav>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
          
          <li className="nav-item">
            <NavLink>      <input type="text" className='form-control' />
                   </NavLink>
          </li>





            
          
          <li className="nav-item">
            <NavLink  className={({ isActive  }) =>
                      isActive
                        ? " nav-link"             : 'nav-link'                                            
                    } >
                      <i className='fab fa-facebook'></i>
                    </NavLink>
          </li>
          <li className="nav-item">
            <NavLink  className={({ isActive  }) =>
                      isActive
                        ? " nav-link"             : 'nav-link'                                            
                    } >
                      <i className='fab fa-youtube'></i>
                    </NavLink>
          </li>
          <li className="nav-item">
            <NavLink  className={({ isActive  }) =>
                      isActive
                        ? " nav-link"             : 'nav-link'                                            
                    } >
                      <i className='fab fa-twitter'></i>
                    </NavLink>
          </li>

         


         

        
           
          
          
        </ul>     

              
        
      </div>
    </div>
  </nav>

  
  </>
  )
}
