import logo from './logo.svg';
import './App.css';
import Navbar from './Navbar/Navbar';
import Layout from './Layout/Layout';
import { createBrowserRouter, RouterProvider, createHashRouter, Navigate} from 'react-router-dom';
// import { hildren } from 'react-router-dom';
// import { Children } from 'react';
import Home from './Home/Home';
// import About from './About/About';
import Login from './Login/Login';
import Movies from './Movies/Movies';
import Register from './Register/Register';
import People from './People/People';
import TvShow from './TvShow/TvShow'
import Moviedetails from './Moviedetails/Moviedetails';
import { Offline, Online } from "react-detect-offline";
import { useEffect, useState } from 'react';
import jwtDecode from 'jwt-decode';
import Protectedroute from './Protectedroute';




function App() {
  let [userData, setuserData]=useState(null)
let saveUserData=()=>{
  let encodedToken=localStorage.getItem('token');
  let decodedToken=jwtDecode(encodedToken);
  console.log(decodedToken)
  setuserData(decodedToken)

}

let logOut=()=>{
  localStorage.removeItem('token');
  setuserData(null);
//  return <Navigate to='Login'/>
}


useEffect(()=>{
  if(localStorage.getItem('token')){
    saveUserData();
  }
},[])






 let [isLogin, setisLogin ] = useState(false);


  const routs = createHashRouter([
    {path : '/', element: <Layout userData={userData}  isLogin={isLogin}  setisLogin={setisLogin } logOut={logOut} />, children:[
      {index:true, element:<Register/>},
      // {path:'about', element:<About/>},
      {path:'moviedetails/:movieid', element:<Moviedetails/>},
      {path:'login', element:<Login saveUserData={saveUserData} setisLogin={setisLogin}/>},
      {path:'movies', element: <Protectedroute><Movies/></Protectedroute>},
      {path:'home', element: <Protectedroute><Home/></Protectedroute> },
      {path:'people', element:    <Protectedroute><People/></Protectedroute>},
      {path:'tvshow', element: <TvShow/>    },
      {path:'*', element:<Protectedroute><h1>not found</h1></Protectedroute>}
    ]}
  
  ])
  return (
   <>
   
   
   <Online><RouterProvider router={routs}/></Online>
    <Offline>you are offline</Offline>
    
   
   
   

   </>
  );
}

export default App;
