import axios from 'axios'
import React, { useEffect } from 'react'
import { useState } from 'react'
import { useParams } from 'react-router-dom'

export default function Moviedetails() {


  let {movieid} = useParams() //hia5od el varibale ely fi path
  let [movieObject, setMovies] =useState({})

  async function getMovieDetail(id){
    let {data} = await axios.get('https://api.themoviedb.org/3/movie/'+id+'?api_key=cbe20ea144a5d106427e5ba1cb975d2a&language=en-US')
    console.log(data)
    setMovies(data)
  }

  useEffect(()=>{
    getMovieDetail(movieid)
  }, [])
  return (
    <div className='container'>
      <div className='row'>
        <div className='col-md-6'>
          <div className='mt-5 details'>
            <img src={`https://image.tmdb.org/t/p/w500${movieObject.poster_path}`} alt="" />
          </div>
        </div>

        

        <div className='col-md-6'>
          <div className='mt-5 details'>
            <h3>{movieObject.title}</h3>
            <p>{movieObject.tagline}</p>
           <button className='btn btn-danger my-1 mx-1'>{movieObject.adult}</button>
           <button className='btn btn-danger my-1 mx-1'>ffffffff</button>
           <button className='btn btn-danger my-1 mx-1'>ffffffff</button>
           <p>vote: {movieObject.vote_average}</p>
           <p>vote Count: {movieObject.vote_count}</p>
           <p>popularity: {movieObject.popularity}</p>
           <p>release_date: {movieObject.release_date}</p>
          </div>
        </div>
      </div>
    </div>
  )
}
