import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react'
import axios from 'axios';
import Joi from 'joi';
import { Link, NavLink } from 'react-router-dom'
import { useNavigate } from 'react-router-dom';

export default function Login({saveUserData}) {
  

  const [user, setUser] = useState({
    
    email:'',
    password:''
  });

  const [apiMsg, setMsg] = useState('');
  const [validationErrors, setErrors] = useState([]);

  let redirect = useNavigate();

  var myUser = {...user}; //make deep copy mn el state
  useEffect(()=>{
    if(user !=={
      first_name:'',
    last_name:'',
    age:'',
    email:'',
    password:''
    })
    console.log(user)
  },[user])

  
  function validateUserData(){

    let rules = Joi.object({
      
      email:Joi.string().email({minDomainSegments:2, tlds: {allow: ['com']}}).required(),
      password: Joi.string().pattern(/^[A-Z]/).required()
    })
    // match or not match
    let validationResult = rules.validate(user, {abortEarly:false});  //kan bi5osh 3l awl error w ytla3 kda higeeb kol el error  
    console.log(validationResult)

    if(validationResult.error !==undefined){ // fi mshkla
      setErrors(validationResult.error.details)
      // showMsg();
     return false
    }
    else{
      //mafish mshkla
      setErrors([])
      return true;
    }

    
  }



  async function  login(){

    if (validateUserData()){



     let {data} = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signin', user);
   console.log(data) ;


  //  if(data.errors !== undefined){
   //law  fi  error
if (data.message === 'success'){
  // props.setisLogin(true); //hna el navbar hi7s bl ta8eer 3shn 7asl update ll props w kam bi7slo lma usestate
  localStorage.setItem('token', data.token)
  saveUserData();
  redirect('/home')

}
  //  setMsg(data.errors.email.message)}

   //law mafish error

   else{
     setMsg(data.message)
    //  navigateTo('/Login')
     
   }
     
    } 
    
     
    
   

   
 }
  function showMsg(input){
    let x = validationErrors.filter((err)=>{return err.message.includes(input)})
    console.log(x)
    return
     <p>{x[0].message}</p>
    
   }



  
  return (
      
     <>
     <div className='container'>
      <h1 className='mt-5'>Login form</h1>
      {validationErrors.map((error)=>{
        if (error.message.includes('pattern')){
          error.message = 'password must start with capital letter'
        }
        return <p className='text-danger'>{error.message}</p>})}

      
      <form onSubmit={(e)=>{
       e.preventDefault();
       login();
      }}>
        

        <label>email</label>
        <input onChange={(e)=>{
          myUser.email=e.target.value; 
          setUser(myUser);
          // console.log(user)
        }} type="text" className='form-control mb-2' />

        <label>password</label>
        <input onChange={(e)=>{
          myUser.password=e.target.value;
          setUser(myUser);
          // console.log(user)
        }} type="password" className='form-control mb-2' />

        <button className='btn btn-info'>Login</button>
        <h2>{apiMsg}</h2>
      </form>
    </div>
     </>

  )
}
