import axios from 'axios';
import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react'
import Moviedetails from '../Moviedetails/Moviedetails';
// import { Link } from 'react-router-dom';

export default function Home() {

 let [moviesContainer, SetData]=useState([]);
 let [tvContainer, setTv] = useState([]);
 let [personContanier, setPerson]= useState([]);

 async function getData(mediaType, setFunction){
  let {data} = await axios.get('https://api.themoviedb.org/3/trending/'+mediaType+'/day?api_key=cbe20ea144a5d106427e5ba1cb975d2a')
  console.log(data.results)
  setFunction(data.results)

  
 }

//  async function getPerson(){
//    let {data} = await axios.get('https://api.themoviedb.org/3/trending/person/day?api_key=cbe20ea144a5d106427e5ba1cb975d2a')
//    console.log(data.results)
//    setPerson(data.results)

  
//  }


//  async function getMovie(){
//     let {data} = await axios.get('https://api.themoviedb.org/3/trending/movie/day?api_key=cbe20ea144a5d106427e5ba1cb975d2a')
//     console.log(data.results)
//     SetData(data.results)

    
//   }







  useEffect(()=>{
    getData('movie',  SetData);
    getData('tv', setTv);
    getData('person', setPerson);
    
    
  },[])
  return (
    <>

    
    <div className="container">
      <div className="row">
        <div className="col-md-4">

          <div className='mt-5'>
            <h1>trending<br/> movies <br/> to watch now</h1>
          </div>

        </div>

        {moviesContainer.slice(0, 10).map((movie)=>{
          return(
          <div className='col-md-2 mt-5'>
          <div className=' text-muted position-relative'>

              <img src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`} alt=""  className='w-100'/>
           


            <h4>{movie.title}</h4>
            <div className='rate position-absolute top-0 end-0 bg-danger py-1 px-1 text-white'>{movie.vote_average}</div>
          </div>
        </div>
        )})}



       

      </div>
    </div>




    <div className="container">
      <div className="row">
        <div className="col-md-4">

          <div className='mt-5'>
            <h1>trending<br/> tv <br/> to watch now</h1>
          </div>

        </div>

        {tvContainer.slice(0, 10).map((series)=>{
          return(
          <div className='col-md-2 mt-5'>
          <div className=' text-muted position-relative'>
            <img src={`https://image.tmdb.org/t/p/w500${series.poster_path}`} alt=""  className='w-100'/>
            <h4>{series.name}</h4>
            <div className='rate position-absolute top-0 end-0 bg-danger py-1 px-1 text-white'>{series.vote_average}</div>
          </div>
        </div>
        )})}



       

      </div>
    </div>


    <div className="container">
      <div className="row">
        <div className="col-md-4">

          <div className='mt-5'>
            <h1>trending<br/> person <br/> to watch now</h1>
          </div>

        </div>

        {personContanier.slice(0, 10).map((person)=>{
          return(
          <div className='col-md-2 mt-5'>
          <div className=' text-muted position-relative'>
        
          <img src={`https://image.tmdb.org/t/p/w500${person.profile_path}`} alt=""  className='w-100'/>
          


          <h4 >{person.original_name}{person.name}</h4>
          <h4>{person.id}</h4>
          <div className='rate position-absolute top-0 end-0 bg-danger py-1 px-1 text-white'>{person.vote_average}</div>
        </div>
        </div>
        )})}



       

      </div>
    </div>
    </>
  )
}
