import React from 'react'
import { useState, useEffect } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import {Helmet} from "react-helmet";


export default function Movies() {


  let [moviesContainer, SetData]=useState([]);
  
  


  async function getPerson(){
    let {data} = await axios.get('https://api.themoviedb.org/3/trending/movie/day?api_key=cbe20ea144a5d106427e5ba1cb975d2a')
    console.log(data.results)
    SetData(data.results)
 
   
  }
     
  useEffect(()=>{
    
    
    
    getPerson()
   
  },[])
  



  return (
    <>
      <div className="application">
            <Helmet>
                <meta charSet="utf-8" />
                <title>Movies</title>
                
            </Helmet>
    <div className="row">
      <div className="col-md-4">

        <div className='mt-5'>
          <h1>trending<br/> movies <br/> to watch now</h1>
        </div>

      </div>

      {moviesContainer.map((person)=>{
        return(
        <div className='col-md-2 mt-5'>
        <div className=' text-muted position-relative'>
          <Link to={`/moviedetails/${person.id}`}>
          <img src={`https://image.tmdb.org/t/p/w500${person.poster_path}`} alt=""  className='w-100'/>
          </Link>


          <h4 >{person.original_name}</h4>
          <h4>{person.id}</h4>
          <div className='rate position-absolute top-0 end-0 bg-danger py-1 px-1 text-white'>{person.vote_average}</div>
        </div>
      </div>
      )})}



     

    </div>
  </div>


  
  
  </>
    
  )


  
}



 

