import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react'
import axios from 'axios';
import Joi from 'joi';
import { useNavigate } from 'react-router-dom';


export default function Register() {

  const [user, setUser] = useState({
    name:'',
    phone:'',
    email:'',
    password:'',
    rePassword:'',
  });

  const [apiMsg, setMsg] = useState('');
  const [validationErrors, setErrors] = useState([]);

  let navigateTo = useNavigate();

  var myUser = {...user}; //make deep copy mn el state
  useEffect(()=>{
    if(user !=={
      name:'',
    
    
    email:'',
    password:'',
    rePassword:'',
    phone:'',
    })
    console.log(user)
  },[user])

  async function  register(){

     if (validateUserData()){



      let {data} = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup', user);
    console.log(data) ;


    if(data.errors !== undefined){
    //law  fi  error

    setMsg(data.errors.email.message)}

    //law mafish error

    else{
      // setMsg(data.message)
      navigateTo('/Login')
      
    }
      
     } 
     
      
     
    

    
  }

  function showMsg(input){
   let x = validationErrors.filter((err)=>{return err.message.includes(input)})
   console.log(x)
   return
    <p>{x[0].message}</p>
   
  }

  function validateUserData(){

    let rules = Joi.object({
      name:Joi.string().alphanum().min(3).max(15).required(),
      
      phone:Joi.number().min(5).max(100000000000).required(),
      email:Joi.string().email({minDomainSegments:2, tlds: {allow: ['com']}}).required(),
      password: Joi.string().pattern(/^[A-Z]/).required(),
      rePassword: Joi.string().pattern(/^[A-Z]/).required(),
    })
    // match or not match
    let validationResult = rules.validate(user, {abortEarly:false});  //kan bi5osh 3l awl error w ytla3 kda higeeb kol el error  
    console.log(validationResult)

    if(validationResult.error !==undefined){ // fi mshkla
      setErrors(validationResult.error.details)
      // showMsg();
     return false
    }
    else{
      //mafish mshkla
      setErrors([])
      return true;
    }

    
  }



 

  return (
    <>
    <div className='container'>
      <h1 className='mt-5'>Registration form</h1>
      {validationErrors.map((error)=>{
        if (error.message.includes('pattern')){
          error.message = 'password must start with capital letter'
        }
        return <p className='text-danger'>{error.message}</p>})}

      
      <form onSubmit={(e)=>{
       e.preventDefault();
       register();
      }}>
        <label>name</label>
        {/* //override in my instance */}
        <input onChange={(e)=>{myUser.name=e.target.value;
        setUser(myUser);
        // console.log(user)
        }} type="text" className='form-control mb-2' />

        

        

        

        <label>phone</label>
        <input onChange={(e)=>{myUser.phone=e.target.value;
        setUser(myUser);
        // console.log(user)

        }} type="text" className='form-control mb-2' />

        <label>email</label>
        <input onChange={(e)=>{
          myUser.email=e.target.value;
          setUser(myUser);
          // console.log(user)
        }} type="text" className='form-control mb-2' />

        <label>password</label>
        <input onChange={(e)=>{
          myUser.password=e.target.value;
          setUser(myUser);
          // console.log(user)
        }} type="password" className='form-control mb-2' />


<label>repassword</label>
        <input onChange={(e)=>{
          myUser.rePassword=e.target.value;
          setUser(myUser);
          // console.log(user)
        }} type="password" className='form-control mb-2' />

        <button className='btn btn-info'>Register</button>
        <h2>{apiMsg}</h2>
      </form>
    </div>
    </>
  )
}
